<template>
    <div class="project">
        <div class="loader" v-show="ShowLoader">
            <svg version="1.1" id="loader-1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="40px" height="40px" viewBox="0 0 50 50" style="enable-background:new 0 0 50 50;" xml:space="preserve">
                <path fill="#fff" d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z" transform="rotate(137.96 25 25)">
                    <animateTransform attributeType="xml" attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="0.6s" repeatCount="indefinite"></animateTransform>
                </path>
            </svg>
        </div>
        <navigation></navigation>
        <profile></profile>
        <logo></logo>
        <div class="content_holder container-fluid">
            <form @submit.prevent="Save">
                <h4 class="form_title">
                    درج پروژه جدید
                </h4>
                <p class="form_desc">
                    با استفاده از این فرم شما می توانید اطلاعات پروژه جدید خود را وارد کنید.
                </p>
                <div class="row">
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">عنوان</label>
                        <input class="form-control col-md-9 col-sm-9 col-xs-12" name="title" v-model="project.title" maxlength="58" placeholder="عنوان" /> 
                    </div>
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">توضیح کوتاه</label>
                        <input class="form-control col-md-9 col-sm-9 col-xs-12" name="title" v-model="project.description" maxlength="150" placeholder="توضیح کوتاه" /> 
                    </div>
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">توضیح تکمیلی</label>
                        <quill-editor class="col-md-9 col-sm-9 col-xs-12" v-model="project.content" :options="editorOption" 
                        @change="onEditorChange($event)" @ready="onEditorReady($event)">
                        </quill-editor> 
                    </div>
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">تصویر اصلی</label>
                        <file-base64
                            :multiple="false"
                            :done="GetHeader">
                        </file-base64>
                        <figure>
                            <img :src="project.mainimage" class="img-responsive thumbnail"/>
                            <span class="remove_img" @click="RemoveHeader()">حـذف</span>
                        </figure>
                    </div>
                </div>
                <div class="row">
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">گالری پروژه</label>
                        <file-base64
                            :multiple="false"
                            :done="GetGallery">
                        </file-base64>
                        <figure  v-for="(item, index) in project.gallery">
                            <img :src="item.src" class="img-responsive thumbnail"/>
                            <span class="remove_img" @click="RemoveImage(index)">حـذف</span>
                        </figure>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-3 col-sm-3 col-xs-12"></div>
                    <div class="col-md-9 col-sm-9 col-xs-12" style="padding-right:0px;">
                        <button type="submit" class="btn save">ذخیــره</button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import Menu from './Menu'
import Logo from './Logo'
import Profile from './Profile'
import fileBase64 from './uplaoder'

export default {
    name:'addproject',
    data (){
        return {
            ShowLoader:false,
            project:{
                title:'',
                description:'',
                content:'',
                mainimage:'',
                gallery:[]
            },editorOption: {
                placeholder: 'توضیحات ...',
                modules: {
                    toolbar: [
                        [{ 'header': [1, 2, 3, false] }],
                        ['bold'],
                        [{ 'align': [] }],
                    ],
                    history: {
                        delay: 1000,
                        maxStack: 50,
                        userOnly: false
                    }
                }
            }
        }
    },
    components:{
        'navigation':Menu,
        'profile':Profile,
        'logo':Logo,
        fileBase64 
    },
    created(){
        return this.GetData(),
        document.title = "درج پروژه جدید";
    },
    methods: {
        GetData(){
            this.$http.get('http://panel.hex.team/api/getwidget/project',{
                headers:{
                    'Authorization':localStorage.getItem('Authorization')
                }
            })
            .then(function(res){
                console.log(res.data);
                var item = JSON.parse(res.data.data);
                this.project.title = item.title;
                this.project.description = item.description;
                this.project.content = item.content;
            },function(err){
                console.log(err);
            });
        },
        GetHeader(file){
            // var formData = new FormData();
            // formData.append('file', file.file);
            // this.$http.post('http://panel.hex.team/api/upload', formData, {
            //     headers:{
            //         'Authorization':localStorage.getItem('Authorization')
            //     }
            // }).then(function (response){
            //     this.project.mainimage = response.data.name;
            // }, function (response) {
            //     console.log('Error occurred...');
            // });
            this.project.mainimage = file.base64;
        },
        RemoveHeader(){
            this.project.mainimage = '';
        },
        GetGallery(file){
             this.project.gallery.push({src:file.base64});
            // var formData = new FormData();
            // formData.append('file', file.file);
            // this.$http.post('http://panel.hex.team/api/upload', formData, {
            //     headers:{
            //         'Authorization':localStorage.getItem('Authorization')
            //     }
            // }).then(function (response){
            //     this.project.gallery.push({src:response.data.name});
            // }, function (response) {
            //     console.log('Error occurred...');
            // });
        },
        RemoveImage(index){
            this.project.gallery.splice(index,1);
        },
        onEditorReady(editor) {
            editor.format('align', 'right');
        },
        onEditorChange(editor){
            // console.log(editor);
            // editor.clipboard.addMatcher (Node.ELEMENT_NODE, function (node) {
            //     console.log(node);
            // });
        },
        Save(){
            this.ShowLoader = true;
            console.log(this.project);
            this.$http.post('http://panel.hex.team/api/setwidget',{
                widget:'project',
                data:JSON.stringify(this.project)
            },{
                headers: {
                   'Authorization':localStorage.getItem('Authorization') 
                }
            })
            .then(function(res){
                console.log(res.data);
                if(res.status == 200){
                    this.ShowLoader = false;
                }
            },function(err){
                console.log(err);
            });
        }
    }
}
</script>

<style>
.project .content_holder{
    position: absolute;
    left: 80px;
    top: 100px;
    right: 40px;
    bottom: 80px;
    padding: 0;
    z-index: 99;
}
.project form{
    float: right;
    width: 100%;
}
.project .form_title{
    text-align: right;
    font-family: "IRANSANS BOLD";
    padding-bottom: 10px;
    font-size: 1.5em;
}
.project .form_desc{
    text-align: right;
    padding-bottom: 10px;
    font-size: 1em;
    direction: rtl;
    margin-bottom: 25px;
}
.project form label{
    float: right;
    margin-bottom: 15px;
    text-align: right;
}
.project form .form-group{
    padding-bottom:15px;
}
.project form .form-control{
    text-align: right;
    outline: none;
    box-shadow: none;
    background-color: #f5f5f5;
    border: 1px solid rgba(34,36,38,.15);
    direction: rtl;
    padding: 10px 12px;
}
.project form textarea.form-control{
    height: 80px;
}
.quill-editor{
    padding: 0;
}
.ql-container{
    background-color: #f5f5f5;
    border-radius: 0 0 6px 6px;
}
.ql-editor.ql-blank::before{
    right: 10px;
}
.ql-toolbar.ql-snow{
    border-radius: 6px 6px 0 0;
    background-color: #f5f5f5;
    direction: rtl;
}
.ql-container .ql-editor {
    min-height: 10em;
    padding-bottom: 1em;
    max-height: 15em;
    font-family: "IRANSANS";
}
.ql-snow .ql-picker:not(.ql-color-picker):not(.ql-icon-picker) svg{
    left: 0;
    right: auto!important;
}
.project figure{
    margin: 0;
    padding: 0;
    position: relative;
    max-height: 150px;
    float: right;
    display: block;
}
.project figure:hover .remove_img{
    opacity: 1;
    transition: all .2s ease-in;
}
.project .remove_img{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 20px;
    opacity: 0;
    height: 35px;
    line-height: 35px;
    text-align: center;
    color: #fff;
    font-size: 14px;
    font-family: "IRANSANS BOLD";
    background: #CF000F;
    border-radius: 0 0 5px 5px;
    cursor: pointer;
    transition: all .2s ease-in;
}
.project .remove_img:hover{
    background:#D91E18;
    transition: all .2s ease-in;
}
.project .thumbnail{
    border-radius: 5px;
    border:0;
    height: auto;
    min-height: 150px;
    object-fit: cover;
    object-position: center;
    max-height: 150px;
    padding: 0;
    background: transparent;
    float: right;
    margin-right: 20px;
}
.project .save{
    background: #f1f102;
    color: #000;
    box-shadow: none;
    outline: none;
    padding: 5px 40px;
    font-size: 15px;
    float: right;
    margin-bottom: 50px;
    font-family: 'IRANSANS BOLD';
}
.project .save:hover{
    opacity: .7;
}
.loader{
    position: fixed;
    z-index: 9999;
}
</style>


