<template>
    <div class="about">
        <div class="loader" v-show="ShowLoader">
            <svg version="1.1" id="loader-1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="40px" height="40px" viewBox="0 0 50 50" style="enable-background:new 0 0 50 50;" xml:space="preserve">
                <path fill="#fff" d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z" transform="rotate(137.96 25 25)">
                    <animateTransform attributeType="xml" attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="0.6s" repeatCount="indefinite"></animateTransform>
                </path>
            </svg>
        </div>
        <navigation></navigation>
        <profile></profile>
        <logo></logo>
        <div class="content_holder container-fluid">
            <form @submit.prevent="Save">
                <h4 class="form_title">
                        تدوین اطلاعات دربـاره ما
                </h4>
                <p class="form_desc">
                    با استفاده از این فرم شما می توانید اطلاعات خود را ویرایش کنید.
                </p>
                <div class="row">
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">عنوان</label>
                        <input class="form-control col-md-9 col-sm-9 col-xs-12" name="title" v-model="about.title" maxlength="58" placeholder="عنوان" /> 
                    </div>
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">توصیحات</label> 
                        <!--<quill-editor class="col-md-9 col-sm-9 col-xs-12" v-model="about.description" :options="editorOption" 
                        @change="onEditorChange($event)" @ready="onEditorReady($event)">
                        </quill-editor>-->
                        <textarea class="form-control col-md-9 col-sm-9 col-xs-12" v-model="about.description" spellcheck="false"></textarea>
                    </div>
                    <div class="form-group col-md-12 col-sm-12 col-xs-12">
                        <label class="col-md-3 col-sm-3 col-xs-12">تصویر اصلی</label>
                        <file-base64
                            :multiple="false"
                            :done="GetHeader">
                        </file-base64>
                        <figure>
                            <img :src="about.files[0].src" class="img-responsive thumbnail"/>
                            <span class="remove_img" @click="RemoveHeader()">حـذف</span>
                        </figure>
                        <div class="progress">
                            <div class="progress-bar" role="progressbar" aria-valuenow="0"
                            aria-valuemin="0" aria-valuemax="100">
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="form-group col-md-12 col-sm-12 col-xs-12" v-for="(item, index) in about.teams">
                        <label class="col-md-3 col-sm-3 col-xs-12">{{index+1}} اعضای تیم</label>
                        <file-base64
                            :multiple="false"
                            :done="GetTeam"
                            :index="index">
                        </file-base64>
                        <div class="form-group col-md-3 col-sm-3 col-xs-12">
                            <input class="form-control" name="name" v-model="item.name" maxlength="58" placeholder="نام" />
                        </div>
                        <div class="form-group col-md-3 col-sm-3 col-xs-12"> 
                            <input class="form-control" name="position" v-model="item.position" maxlength="58" placeholder="سمت" /> 
                        </div>
                        <div class="clearfix"></div>
                        <div class="col-md-3 col-sm-3 col-xs-12"></div>
                        <figure>
                            <img :src="about.files[index+1].src" class="img-responsive thumbnail"/>
                            <span class="remove_img" @click="RemoveImage(index+1)">حـذف</span>
                        </figure>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-3 col-sm-3 col-xs-12"></div>
                    <div class="col-md-9 col-sm-9 col-xs-12" style="padding-right:0px;">
                        <button type="submit" class="btn save">ذخیــره</button>
                    </div>
                </div>
            </form>
        </div>        
    </div>
</template>

<script>
import Menu from './Menu'
import Logo from './Logo'
import Profile from './Profile'
import fileBase64 from './uplaoder'

export default {
    name:'about',
    data (){
        return {
            ShowLoader:false,
            Loader:'',
            LoaderWidth:0,
            about:{
                title : '',
                description : '',
                header:'',
                teams:[
                    {
                        name:'Eng.Mehdi Mohammadi',
                        position:'CEO'
                    },
                    {
                        name:'Eng.Mehdi Mohammadi',
                        position:'CEO'
                    }
                ],
                files:[{
                    src:''
                },{
                    src:''
                },{
                    src:''
                }]
            },
            editorOption: {
                placeholder: 'توضیحات ...',
                modules: {
                    toolbar: [
                        [{ 'header': [1, 2, 3, false] }],
                        ['bold'],
                        [{ 'align': [] }],
                    ],
                    history: {
                        delay: 1000,
                        maxStack: 50,
                        userOnly: false
                    }
                }
            }
        }
    },
    components:{
        'navigation': Menu,
        'logo': Logo,
        'profile': Profile,
        fileBase64 
    },
    // computed :{
    //     FileAddress (){
    //         return 'http://panel.hex.team/api/download'+src;
    //     }
    // },
    created(){
        return this.GetData(), 
        document.title = "تدوین صفحه درباره ما";
    },
    methods :{
        GetData(){
            this.$http.get('http://panel.hex.team/api/getwidget/about',{
                headers:{
                    'Authorization':localStorage.getItem('Authorization')
                }
            })
            .then(function(res){
                console.log(res.data);
                var item = JSON.parse(res.data.data);
                this.about.title = item.title;
                this.about.description = item.description;
                this.about.teams = item.teams;
                this.about.files = item.files;
            },function(err){
                console.log(err);
            });
        },
        GetHeader(file){
            this.header = file.base64;
            var formData = new FormData();
            formData.append('file', file.file);
            this.$http.post('http://panel.hex.team/api/upload', formData, {
                headers:{
                    'Authorization':localStorage.getItem('Authorization')
                },
                progress(e) {
                    if (e.lengthComputable) {
                        console.log($('.progress-bar'));
                        $('.progress-bar').css('width',parseInt((e.loaded / e.total ) * 100));
                        this.Loader = true;
                    }
                }
            }).then(function (response){
                this.Loader = false;
                this.about.files[0].src = 'http://panel.hex.team/api/download/' + response.data.name;
            }, function (response) {
                console.log('Error occurred...');
            });
        },
        GetTeam(file){
            this.files[file.index].thumbnail = file.base64;
            var formData = new FormData();
            formData.append('file', file.file);
            this.$http.post('http://panel.hex.team/api/upload', formData, {
                headers:{
                    'Authorization':localStorage.getItem('Authorization')
                }
            }).then(function (response){
                this.about.files[file.index+1].src = 'http://panel.hex.team/api/download/' + response.data.name;
            }, function (response) {
                console.log('Error occurred...');
            });
        },
        RemoveHeader(){
             this.about.files[0].src = '';
        },
        RemoveImage(index){
             this.about.files[index].src = '';
        },
        Save(){
            console.log(this.about);
            this.ShowLoader = true;
            this.$http.post('http://panel.hex.team/api/setwidget',{
                widget:'about',
                data:JSON.stringify(this.about)
            },{
                headers: {
                   'Authorization':localStorage.getItem('Authorization') 
                }
            })
            .then(function(res){
                if(res.status == 200){
                    this.ShowLoader = false;
                }
                console.log(res.data);
            },function(err){
                console.log(err);
            });
        }
    }
}
</script>

<style>
.about .content_holder{
    position: absolute;
    left: 80px;
    top: 100px;
    right: 40px;
    bottom: 80px;
    padding: 0;
    z-index: 99;
}
.about form{
    float: right;
    width: 100%;
}
.about .form_title{
    text-align: right;
    font-family: "IRANSANS BOLD";
    padding-bottom: 10px;
    font-size: 1.5em;
}
.about .form_desc{
    text-align: right;
    padding-bottom: 10px;
    font-size: 1em;
    direction: rtl;
    margin-bottom: 25px;
}
.about form label{
    float: right;
    margin-bottom: 15px;
    text-align: right;
}
.about form .form-group{
    padding-bottom:15px;
}
.about form .form-control{
    text-align: right;
    outline: none;
    box-shadow: none;
    background-color: #f5f5f5;
    border: 1px solid rgba(34,36,38,.15);
    direction: rtl;
    padding: 10px 12px;
}
.about form textarea.form-control{
    height: 150px;
}
.quill-editor{
    padding: 0;
}
.ql-container{
    background-color: #f5f5f5;
    border-radius: 0 0 6px 6px;
}
.ql-editor.ql-blank::before{
    right: 10px;
}
.ql-toolbar.ql-snow{
    border-radius: 6px 6px 0 0;
    background-color: #f5f5f5;
    direction: rtl;
}
.ql-container .ql-editor {
    min-height: 10em;
    padding-bottom: 1em;
    max-height: 15em;
    font-family: "IRANSANS";
}
.ql-snow .ql-picker:not(.ql-color-picker):not(.ql-icon-picker) svg{
    left: 0;
    right: auto!important;
}
.about figure{
    margin: 0;
    padding: 0;
    position: relative;
    max-height: 150px;
    float: right;
    display: block;
}
.about figure:hover .remove_img{
    opacity: 1;
    transition: all .2s ease-in;
}
.about .remove_img{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 20px;
    opacity: 0;
    height: 35px;
    line-height: 35px;
    text-align: center;
    color: #fff;
    font-size: 14px;
    font-family: "IRANSANS BOLD";
    background: #CF000F;
    border-radius: 0 0 5px 5px;
    cursor: pointer;
    transition: all .2s ease-in;
}
.about .remove_img:hover{
    background:#D91E18;
    transition: all .2s ease-in;
}
.about .thumbnail{
    border-radius: 5px;
    border:0;
    height: auto;
    min-height: 150px;
    object-fit: cover;
    object-position: center;
    max-height: 150px;
    padding: 0;
    background: transparent;
    float: right;
    margin-right: 20px;
}
.about .save{
    background: #f1f102;
    color: #000;
    box-shadow: none;
    outline: none;
    padding: 5px 40px;
    font-size: 15px;
    float: right;
    margin-bottom: 50px;
    font-family: 'IRANSANS BOLD';
}
.about .save:hover{
    opacity: .7;
}
.loader{
    position: fixed;
    z-index: 9999;
}
</style>


